
<template>
    <div>
    <!--for demo wrap-->
    <button v-if="this.$session.get('student_id') === '21500670'" v-on:click="update_course()"> </button>
    <button v-if="this.$session.get('student_id') === '21500670'" v-on:click="update_user()">  </button>

    <h1> </h1>
    <div class="tbl-header">
        <table cellpadding="0" cellspacing="0" border="0">
        <thead>
                <tr>  
                    <th id="h_index"></th>
                    <th id="h_ttname"> </th>
                    <th id="h_credit"></th>
                    <th id="h_change"></th>
                </tr>
                
        </thead>
        </table>
    </div>
    <div class="tbl-content">
        <table cellpadding="0" cellspacing="0" border="0">
            <tbody>
                <div v-for="(ttlist,key) in this.ttlists" :key="key">
                    <tr>
                        <td id="index">{{ key+1 }}</td>
                        <td  v-on:click = " $EventBus.$emit('to_timetables',ttlist)" id = "ttname">{{ttlist.ttname}}<button id="modify_name" v-on:click="modify_name(key)"></button></td>
                        <td id="credit">{{ttlist.total_credit}}</td>
                        <td id="edit"><button class ="change" v-on:click="ttedit(key)"></button></td>
                        <td id="del"><button class ="change" v-on:click="ttdelete(key)"></button></td>
                    </tr>

                    <hr />
                </div> 
            </tbody>
        </table>
    </div><!--tbl-content ending tag-->
    <div class="add">
            <button id="add" v-on:click="go_make()"> </button>
    </div>
  
    </div>
</template>
<script>
    export default {
        name: 'ttlist',
        //props :['ttlists'],
        data() {
            return {
                ttlists:[],
                ttlist:{
                    ttname :"",
                    ttrank:"",
                    total_credit:""
                },
                ttnames:[]
            };
        },
        
        created () {
            this.$http.post('/api/show', {
                student_id : this.$session.get('student_id')
            }).then((response) => {
                if (response.status === 200 ) {
                    this.ttlists = response.data; //ttname, ttrank, total_credit
                    this.$EventBus.$emit('to_timetables',this.ttlists[0].ttname);// show    default    
                }
            });
        },
        methods: {
            update_course(){
                console.log('course update in');
                this.$http.get('api/course_update');
            },
            update_user(){
                console.log('course update in');
                this.$http.get('api/course_update/user');
            },
            go_make() { //    
                var userInput=prompt("   ");
                if(userInput==""){
                    alert("   ");
                    return false;
                } else if(userInput==null){
                    alert("");
                    return false;
                } else if(this.duplication(userInput)){
                    alert("  ");
                    return false;
                } else {
                 this.$session.set('to_timetablem', userInput)//   
                }
                // this.$router.replace({name: 'make'});
                window.location = 'http://203.252.99.231/make'
            },
            modify_name(key) {//  ()-
                var modified_name = prompt("   ");
                if(modified_name === "") {
                    alert("    ");
                    return false;
                } else if(modified_name === null) {
                    console.log(ttname);
                    alert("");
                    return false;
                } else if(this.duplication(modified_name)){
                    alert("  ");
                    return false;
                } else {//
                    this.$http.post('/api/show/modify_ttname', {
                        student_id :  this.$session.get('student_id'),
                        original_ttname :  this.ttlists[key].ttname,
                        modified_ttname : modified_name 
                    })
                    this.ttlists[key].ttname = modified_name 
                }
            },
            duplication(new_name){// ,    
            var duplication =   this.ttlists.some(function(item, index, array) {
                                    return (item.ttname === new_name);
                                });
                return duplication;
            },
            ttdelete(key){// 
                if(confirm(" ?")){
                    console.log(this.ttlists[key].ttname);

                        this.$http.post('/api/show/del_tt', {
                            student_id :  this.$session.get('student_id'),
                            ttname :  this.ttlists[key].ttname
                    })
                    this.ttlists.splice(key,1);
                }
            },
            ttedit(key){// 
                if(confirm(" ?")){        
                    this.$session.set('to_timetablem', this.ttlists[key].ttname)//   
                    window.location = 'http://203.252.99.231/make'
                }  
            },
            ttselect(key){
                console.log(this.ttlists[key].ttname);
                this.$EventBus.$emit('to_timetables',this.ttlists[key].ttname);
            }
        },
    }
    
</script>



<style  src = '../../assets/Showpage/list.less' lang ="scss" scoped>
</style>


